	module.exports = {"data":{"markdownRemark":{"html":"<p>Sometimes after looking into a program, you realize it’s not a program you’re interested in participating in. You have the option to leave a program. The <strong>Leave Program</strong> button will appear on the security page of the program you want to leave. to leave the program, click <strong>Leave Program</strong>. </p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-946ca.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 34.19257012888552%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"leaving-programs-1\"\n        title=\"\"\n        src=\"/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-e7ffa.png\"\n        srcset=\"/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-49813.png 125w,\n/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-dc8a5.png 250w,\n/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-e7ffa.png 500w,\n/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-dda24.png 750w,\n/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-c5f18.png 1000w,\n/docs.hackerone.com/static/leaving-programs-1-a33d17157e4c9ecef34623e4eb558e4e-946ca.png 1319w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>After selecting to leave the program, you'll get the option to fill out a questionnaire on why you've decided to leave the program. Filling out the questionnaire will put you as a priority for future invites. See: <a href=\"/docs.hackerone.com/hackers/priority-invitations\">Priority Invitations</a></p>","frontmatter":{"path":"/programs/leaving-programs","title":"Leaving Programs"}}},"pathContext":{}}

